<template>
  <v-app>
    <Navbar />
    <v-main class="d-md-none">
      <router-view />
    </v-main>
    <Footer />
  </v-app>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Footer from "@/components/Footer.vue";
import { mapState, mapActions } from "vuex";
import api from "./api/api";

export default {
  name: "App",
  data: () => ({
    //
  }),
  computed: {
    ...mapState(["user"]),
    userExists() {
      return Object.keys(this.user).length;
    },
  },
  async mounted() {
    //check how to change the header before every api call and not only at the start of the program
    try {
      const user = (await api.users().userExists()).data;
      this.updateUser(user);
    } catch (err) {
      console.log(err);
      this.$router.push({ name: "Login" });
    }
  },
  methods: {
    ...mapActions(["updateColor", "updateUser"]),
  },
  components: {
    Navbar,
    Footer,
  },
};
</script>
