<template>
  <v-row dense>
    <v-col>
      <v-sheet height="7vh">
        <v-toolbar class="d-flex justify-space-around" flat>
          <v-btn outlined :color="color" @click="setToday"> Today </v-btn>
          <v-btn fab text small :color="color" @click="prev">
            <v-icon small> mdi-chevron-left </v-icon>
          </v-btn>
          <v-btn fab text small :color="color" @click="next">
            <v-icon small> mdi-chevron-right </v-icon>
          </v-btn>
          <v-menu>
            <template v-slot:activator="{ on, attrs }">
              <v-btn outlined :color="color" v-bind="attrs" v-on="on">
                <span class="text-caption">{{ type }}</span>
                <v-icon right> mdi-menu-down </v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                v-for="currentType in Object.keys(types)"
                :key="currentType"
                @click="type = currentType"
              >
                <v-list-item-title>{{ currentType }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          <v-btn @click="showEventDialog = true" icon>
            <v-icon>mdi-plus</v-icon>
          </v-btn>
          <EventDialog
            :showEventDialog="showEventDialog"
            @create-event="createEvent"
            @close-event-dialog="closeEventDialog"
          />
        </v-toolbar>
      </v-sheet>
      <v-sheet height="80vh">
        <v-calendar
          ref="calendar"
          v-model="focus"
          :color="color"
          :events="events"
          :event-color="getEventColor"
          :type="type"
          @click:event="showEvent"
          @click:more="viewDay"
          @click:date="viewDay"
          @change="updateRange"
        ></v-calendar>
        <v-menu
          v-model="selectedOpen"
          :close-on-content-click="false"
          :activator="selectedElement"
          offset-x
        >
          <v-card color="grey lighten-4" min-width="350px" flat>
            <v-toolbar :color="selectedEvent.color" dark>
              <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
              <v-spacer></v-spacer>
              <v-btn icon>
                <v-icon>mdi-dots-vertical</v-icon>
              </v-btn>
            </v-toolbar>
            <v-card-text v-if="selectedEvent.user"
              >Event submitted by
              <b>{{ selectedEvent.user.username }}</b></v-card-text
            >
            <v-card-actions>
              <v-btn text color="secondary" @click="selectedOpen = false">
                Cancel
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
      </v-sheet>
    </v-col>
  </v-row>
</template>
<script>
import { mapState } from "vuex";
import EventDialog from "@/components/EventDialog.vue";
import api from "../api/api";
import Swal from "sweetalert2";

export default {
  data: () => ({
    focus: "",
    type: "month",
    types: {
      month: "Month",
      week: "Week",
      day: "Day",
    },
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    showEventDialog: false,
  }),
  computed: {
    ...mapState(["color"]),
  },
  components: {
    EventDialog,
  },
  mounted() {
    this.$refs.calendar.checkChange();
  },
  methods: {
    viewDay({ date }) {
      this.focus = date;
      this.type = "day";
    },
    fireSwal(icon, title) {
      Swal.fire({
        icon,
        title,
        showConfirmButton: false,
        timer: 1000,
      });
    },
    getEventColor(event) {
      return event.color;
    },
    setToday() {
      this.focus = "";
    },
    prev() {
      this.$refs.calendar.prev();
    },
    next() {
      this.$refs.calendar.next();
    },
    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event;
        this.selectedElement = nativeEvent.target;
        requestAnimationFrame(() =>
          requestAnimationFrame(() => (this.selectedOpen = true))
        );
      };

      if (this.selectedOpen) {
        this.selectedOpen = false;
        requestAnimationFrame(() => requestAnimationFrame(() => open()));
      } else {
        open();
      }

      nativeEvent.stopPropagation();
    },
    async updateRange({ start, end }) {
      const min = new Date(`${start.date}T00:00:00`);
      const max = new Date(`${end.date}T23:59:59`);

      try {
        const events = (await api.events().getEventsByDateRange(min, max)).data;

        this.events = events.map((event) => {
          event.start = new Date(event.start);
          event.end = new Date(event.end);
          event.timed = true;

          return event;
        });
      } catch (err) {
        this.fireSwal("error", "Error loading events");
      }
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },

    async createEvent(event) {
      this.showEventDialog = false;

      try {
        await api.events().addEvent(event);
      } catch (err) {
        this.fireSwal("error", "Error adding event");
      }
    },
    closeEventDialog() {
      this.showEventDialog = false;
    },
  },
};
</script>

