<template>
  <v-row justify="center">
    <v-dialog v-model="showEventDialog" persistent max-width="600px">
      <v-card ref="form">
        <v-card-title>
          <span class="text-h5">{{ eventName ? eventName : "New Event" }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  label="What are we doin?"
                  v-model="eventName"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <DatePicker
                  label="When does this event start?"
                  @pick-date="pickStartDate"
                />
              </v-col>
              <v-col cols="12">
                <TimePicker
                  label="At what time does it start?"
                  @pick-time="pickStartTime"
                />
              </v-col>
              <v-col cols="12">
                <DatePicker
                  label="When does this event end?"
                  @pick-date="pickEndDate"
                />
              </v-col>
              <v-col cols="12">
                <TimePicker
                  label="At what time does it end?"
                  @pick-time="pickEndTime"
                />
              </v-col>
              <v-col cols="12">
                <v-color-picker
                  dot-size="30"
                  v-model="eventColor"
                ></v-color-picker>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="$emit('close-event-dialog')"
          >
            Close
          </v-btn>
          <v-btn color="blue darken-1" text @click="submit"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import DatePicker from "@/components/DatePicker.vue";
import TimePicker from "@/components/TimePicker.vue";
import Swal from "sweetalert2";
import { mapState } from "vuex";

export default {
  name: "EventDialog",
  data: () => ({
    eventStartDate: null,
    eventEndDate: null,
    eventStartTime: null,
    eventEndTime: null,
    eventName: "",
    eventColor: "",
  }),
  computed: {
    ...mapState(["user"]),
    start() {
      return new Date(`${this.eventStartDate}T${this.eventStartTime}`);
    },
    end() {
      return new Date(`${this.eventEndDate}T${this.eventEndTime}`);
    },

    event() {
      return {
        name: this.eventName,
        user: this.user,
        start: this.start,
        end: this.end,
        color: this.eventColor,
      };
    },
  },

  props: {
    showEventDialog: {
      type: Boolean,
    },
  },
  components: {
    DatePicker,
    TimePicker,
  },
  methods: {
    pickStartDate(date) {
      this.eventStartDate = date;
    },
    pickEndDate(date) {
      this.eventEndDate = date;
    },
    pickStartTime(time) {
      this.eventStartTime = time;
    },
    pickEndTime(time) {
      this.eventEndTime = time;
    },
    formValid() {
      if (
        !this.eventName ||
        !this.eventStartDate ||
        !this.eventEndDate ||
        !this.eventStartTime ||
        !this.eventEndTime ||
        !this.eventColor ||
        this.start < new Date() ||
        this.end <= this.start
      ) {
        return false;
      }

      return true;
    },
    fireSwal(icon, title) {
      Swal.fire({
        icon,
        title,
        showConfirmButton: false,
        timer: 1500,
      });
    },
    submit() {
      if (this.formValid()) {
        this.$emit("create-event", this.event);
        this.fireSwal("success", `Added event ${this.eventName}`);
        this.$router.go(0);
      } else {
        this.fireSwal("error", "Please fill form correctly");
      }
    },
  },
};
</script>

