import axios from "axios";

axios.defaults.headers.common['Authorization'] = `Bearer ${localStorage.getItem("userId")}`;

if (process.env.NODE_ENV !== 'production') {
    axios.defaults.baseURL = "http://192.168.1.39:3000"
}
export default {
    users() {
        return {
            login: (username, password) => {
                return axios.post("/users/login", { username, password })
            },
            userExists: () => {
                return axios.get("/users/exists");
            },
            userGroup: (userId) => {
                return axios.get(`/users/${userId}/group`)
            },
            updateUserStatus: (userId, status) => {
                axios.patch(`/users/${userId}/status`, { status })
            },
            updateUserPhrase: (userId, phrase) => {
                axios.patch(`/users/${userId}/phrase`, { phrase })
            },
            updateUserPassword: (userId, password) => {
                axios.patch(`/users/${userId}/password`, { password })
            }
        }
    },
    events() {
        return {
            addEvent: (event) => {
                return axios.post("/events", event)
            },
            getEventsByDateRange: (start, end) => {
                return axios.get(`/events/${start}/${end}`)
            }
        }
    },
    groups() {
        return {
            addGroup: (users) => {
                return axios.post("/groups", { users })
            }
        }
    },
    posts() {
        return {
            addPost: (formData) => {
                axios.defaults.headers.common['Authorization'] = `Bearer ${localStorage.getItem("userId")}`;
                return axios.post("/posts", formData)
            },
            allPosts: () => {
                return axios.get("/posts")
            },
            likePost: (postId) => {
                axios.defaults.headers.common[
                    "Authorization"
                ] = `Bearer ${localStorage.getItem("userId")}`;
                return axios.patch(`/posts/like/${postId}`)
            },
            unlikePost: (postId) => {
                axios.defaults.headers.common[
                    "Authorization"
                ] = `Bearer ${localStorage.getItem("userId")}`;
                return axios.patch(`/posts/unlike/${postId}`)
            }
        }
    }
}