<template>
  <v-dialog
        ref="dialog"
        v-model="modal"
        :return-value.sync="time"
        persistent
        width="290px"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="time"
            :label="label"
            prepend-icon="mdi-clock-time-four-outline"
            readonly
            v-bind="attrs"
            v-on="on"
          ></v-text-field>
        </template>
        <v-time-picker
        ref="eventTime"
          v-if="modal"
          v-model="time"
          full-width
        >
          <v-spacer></v-spacer>
          <v-btn
            text
            color="primary"
            @click="modal = false"
          >
            Cancel
          </v-btn>
          <v-btn
            text
            color="primary"
            @click="pickTime"
          >
            OK
          </v-btn>
        </v-time-picker>
      </v-dialog>
</template>

<script>
  export default {
      name:"TimePicker",
    data () {
      return {
        time: null,
        modal: false,
      }
    },
    props:{
      label:String
    },
     methods:{
        pickTime(){
            this.$refs.dialog.save(this.time)
            this.$emit('pick-time',this.time);
        }
     }
  }
</script>

