<template>
  <div class="user-item">
    <v-list-item link>
      <v-list-item-avatar>
        <img :src="currentUser.profileUrl" alt="John" />
      </v-list-item-avatar>

      <v-list-item-content>
        <v-list-item-title :class="{ title: user._id === currentUser._id }">{{
          currentUser.username
        }}</v-list-item-title>

        <v-list-item-subtitle>{{ currentUser.phrase }}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <v-divider v-if="!last" inset></v-divider>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "UserItem",
  computed: {
    ...mapState(["user"]),
  },
  props: {
    currentUser: {
      type: Object,
      required: true,
    },
    last: {
      type: Boolean,
      required: true,
      default: false,
    },
  },
};
</script>

