<template>
  <v-footer :color="color" fixed padless>
    <v-row justify="center" no-gutters>
      <v-col
        :class="[color, 'py-1 text-center d-flex justify-space-around']"
        cols="12"
        v-if="userExists"
      >
        <v-btn
          v-for="(icon, iconIndex) in icons"
          :key="iconIndex"
          @click="$router.push({ name: icon.name })"
          icon
        >
          <v-icon :color="icon.active ? 'white' : ''">{{ icon.icon }}</v-icon>
        </v-btn>
      </v-col>
    </v-row>
  </v-footer>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      icons: [
        {
          name: "Calendar",
          icon: "mdi-calendar",
          active: false,
        },
        {
          name: "PhoneBook",
          icon: "mdi-phone",
          active: false,
        },
        {
          name: "Home",
          icon: "mdi-home",
          active: false,
        },
        {
          name: "Group",
          icon: "mdi-map-marker-check",
          active: false,
        },
        {
          name: "Profile",
          icon: "mdi-account",
          active: false,
        },
      ],
    };
  },
  computed: {
    ...mapState(["user", "color"]),
    userExists() {
      return Object.keys(this.user).length;
    },
    currentRouteName() {
      return this.$route.name;
    },
  },
  methods: {
    setIconActive() {
      this.icons.map((icon) => {
        icon.name === this.currentRouteName
          ? (icon.active = true)
          : (icon.active = false);
        return icon;
      });
    },
  },
  mounted() {
    this.setIconActive();
  },
  watch: {
    currentRouteName() {
      this.setIconActive();
    },
  },
};
</script>

