<template>
  <div class="post-card">
    <v-card :class="{ 'mx-auto': true, lastPost: last }" max-width="100vw" flat>
      <v-card-title>
        <v-avatar size="10vw" class="mr-3">
          <img :src="post.author.profileUrl" :alt="post.author.username" />
        </v-avatar>

        {{ post.author.username }}
      </v-card-title>

      <v-img :src="post.url"></v-img>
      <v-card-actions class="pb-0">
        <v-btn width="15vw" class="d-flex justify-space-between" icon>
          <v-icon
            size="8vw"
            @click="likeUnlikePost"
            :color="hasLikedPost ? 'red' : ''"
            >mdi-heart</v-icon
          >
          <span class="subtitle-1">{{ post.likes.length }}</span>
        </v-btn>
      </v-card-actions>
      <v-card-subtitle v-if="post.caption" class="pt-0 pb-2 pl-3">
        <b>{{ post.author.username }}</b> {{ post.caption }}
      </v-card-subtitle>
      <v-card-subtitle v-if="post.uploadDate" class="pt-0 pb-0 pl-3">
        {{ postDate }}
      </v-card-subtitle>
    </v-card>

    <v-divider></v-divider>
  </div>
</template>

<script>
import { mapState } from "vuex";
import api from "@/api/api.js";

export default {
  name: "PostCard",

  computed: {
    ...mapState(["user"]),
    hasLikedPost() {
      return this.post.likes.includes(this.user._id);
    },
    postDate() {
      const date = new Date(this.post.uploadDate);
      const hoursFromToday = Math.abs(date - new Date()) / 36e5;

      if (hoursFromToday < 1) {
        return `${Math.floor(hoursFromToday * 60)} minutes ago`;
      } else if (hoursFromToday < 24) {
        return `${Math.floor(hoursFromToday)} hours ago`;
      }

      return `${date.toLocaleString("en-us", {
        month: "long",
      })} ${date.getDate()}`;
    },
  },
  props: {
    post: {
      type: Object,
      required: true,
    },
    last: {
      type: Boolean,
      required: true,
    },
  },

  methods: {
    async likeUnlikePost() {
      if (!this.hasLikedPost) {
        try {
          this.post.likes.push(this.user._id);
          await api.posts().likePost(this.post._id);
        } catch (err) {
          this.post.likes.splice(this.post.likes.indexOf(this.user._id), 1);
          console.log(err);
        }
      } else {
        try {
          this.post.likes.splice(this.post.likes.indexOf(this.user._id), 1);
          await api.posts().unlikePost(this.post._id);
        } catch (err) {
          console.log(err);
        }
      }
    },
  },
};
</script>

<style scoped>
.lastPost {
  margin-bottom: 5vh;
}
</style>