<template>
  <div class="home">
    <PostCard
      v-for="(post, postIndex) in posts"
      :key="post._id"
      :post="post"
      :last="postIndex === posts.length - 1"
    />
  </div>
</template>

<script>
import api from "../api/api";
import PostCard from "@/components/home/PostCard.vue";

export default {
  name: "Home",
  data() {
    return {
      posts: [],
    };
  },
  async mounted() {
    try {
      this.posts = (await api.posts().allPosts()).data.reverse();
    } catch (err) {
      console.log(err);
    }
  },

  components: {
    PostCard,
  },
};
</script>
