<template>
  <div class="group-page">
    <v-card class="text-center mt-4 mx-auto" max-width="95vw">
      <v-card-subtitle v-if="weekend" class="headline"
        >Weekend Of {{ weekend.toLocaleDateString() }}</v-card-subtitle
      >
      <v-card-text class="pb-2">What Is Your Status?</v-card-text>
      <v-card-actions class="d-flex justify-center">
        <v-btn
          v-for="status in statuses"
          :key="status.name"
          :color="color"
          :disabled="user.status === status.name"
          @click="changeUserStatus(status.name)"
          class="mb-3"
          max-width="32vw"
        >
          {{ status.name }}
          <v-icon right> {{ status.icon }} </v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>

    <v-tabs
      rounded
      class="d-flex justify-center mt-3"
      icons-and-text
      :color="color"
      centered
      v-model="tabs"
    >
      <v-tab
        v-for="status in statuses"
        :key="status.name"
        :class="status.class ? status.class : ''"
        :href="'#' + status.name"
      >
        {{ status.displayName }}
        <v-icon>{{ status.icon }}</v-icon>
      </v-tab>
      <v-tabs-slider :color="color"></v-tabs-slider>
    </v-tabs>
    <v-tabs-items v-model="tabs">
      <v-tab-item
        v-for="status in statuses"
        :key="status.name"
        :value="status.name"
      >
        <v-card max-width="90vw" class="mx-auto my-3">
          <v-list v-if="status.users.length" two-line subheader>
            <UsersSection :users="status.users" />
          </v-list>
          <v-card-title v-else class="d-flex justify-center"
            >Nobody is with status - {{ status.displayName }}</v-card-title
          >
        </v-card>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
import UsersSection from "@/components/group/UsersSection.vue";
import api from "../api/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "GroupPage",
  data() {
    return {
      weekendDay: 6,
      weekend: null,
      users: [],
      tabs: null,
      statuses: [
        {
          name: "Available",
          displayName: "Available",
          icon: "mdi-checkbox-marked-circle",
          users: [],
        },
        { name: "IDF", displayName: "IDF", icon: "mdi-pistol", users: [] },
        {
          name: "Girl",
          displayName: " ",
          icon: "mdi-face-woman",
          users: [],
          class: "hebrew-font",
        },
      ],
    };
  },
  watch: {
    async user() {
      await this.fetchGroup();
    },
  },
  async mounted() {
    this.weekend = this.getWeekendDate();
    if (this.userExists) {
      await this.fetchGroup();
    }
  },
  methods: {
    ...mapActions(["updateUser"]),
    getWeekendDate() {
      const today = new Date();
      const weekendFromToday = this.weekendDay - today.getDay() - 1;

      return new Date(today.setDate(today.getDate() + weekendFromToday));
    },
    addUserToStatus(statusName, user) {
      this.statuses[
        this.statuses.findIndex((status) => status.name === statusName)
      ].users.push(user);
    },
    updateUsersStatuses() {
      this.statuses.forEach((status) => (status.users = []));
      this.users.forEach((user) => {
        switch (user.status) {
          case "Available":
            this.addUserToStatus("Available", user);

            break;
          case "IDF":
            this.addUserToStatus("IDF", user);

            break;
          case "Girl":
            this.addUserToStatus("Girl", user);

            break;
        }
      });
    },
    async changeUserStatus(status) {
      try {
        this.user.status = status;
        this.updateUser(this.user);
        await api.users().updateUserStatus(this.user._id, status);
        this.users[
          this.users.findIndex((user) => user._id === this.user._id)
        ].status = status;
        this.updateUsersStatuses();
      } catch (err) {
        console.log(err);
      }
    },
    async fetchGroup() {
      try {
        const group = (await api.users().userGroup(this.user._id)).data;
        this.users = group.users;
        this.updateUsersStatuses();
      } catch (err) {
        console.log(err);
      }
    },
  },
  computed: {
    ...mapState(["user", "color"]),
    userExists() {
      return Object.keys(this.user).length;
    },
  },
  components: {
    UsersSection,
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Rubik:ital@1&display=swap");

.hebrew-font {
  font-family: "Rubik", Helvetica, Arial;
}

.tab-text {
  color: green;
}
</style>