<template>
  <div class="profile">
    <v-card
      class="mx-auto my-5 d-flex flex-column align-center"
      max-width="90vw"
    >
      <v-avatar size="60vw" class="my-5">
        <img :src="user.profileUrl" />
      </v-avatar>
      <!-- <v-file-input
        label="File input"
        filled
        prepend-icon="mdi-camera"
      ></v-file-input> -->
      <v-card-title>{{ user.username }}</v-card-title>

      <v-card-actions>
        <v-btn icon @click="editingPhrase = !editingPhrase" :color="color">
          <v-icon>mdi-pencil</v-icon>
        </v-btn>
        <v-text-field
          label="Your Phrase"
          append-icon="mdi-content-save"
          :disabled="!editingPhrase"
          :color="color"
          @click:append="saveUserPhrase"
          v-model="userPhrase"
        ></v-text-field>
      </v-card-actions>
      <v-card-actions>
        <!-- <v-btn icon @click="editingPassword = !editingPassword" :color="color">
          <v-icon>mdi-pencil</v-icon>
        </v-btn> -->
        <v-text-field
          :color="color"
          label="Your Password"
          append-icon="mdi-content-save"
          :prepend-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
          :type="showPassword ? 'text' : 'password'"
          @click:prepend="showPassword = !showPassword"
          @click:append="saveUserPassword"
          v-model="userPassword"
        ></v-text-field>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import api from "../api/api";
import Swal from "sweetalert2";

export default {
  name: "Profile",
  data() {
    return {
      editingPhrase: false,
      // editingPassword: false,
      userPhrase: this.$store.state.user.phrase,
      userPassword: this.$store.state.user.password,
      showPassword: false,
    };
  },
  computed: {
    ...mapState(["color", "user"]),
  },
  watch: {
    user() {
      this.userPhrase = this.user.phrase;
      this.userPassword = this.user.password;
    },
  },
  methods: {
    ...mapActions(["updateUser"]),
    fireSwal(icon, title) {
      Swal.fire({
        icon,
        title,
        showConfirmButton: false,
        timer: 1500,
      });
    },
    async saveUserPhrase() {
      try {
        this.user.phrase = this.userPhrase;
        this.updateUser(this.user);
        await api.users().updateUserPhrase(this.user._id, this.userPhrase);
        this.editingPhrase = false;
        this.fireSwal("success", "Phrase changed");
      } catch (err) {
        this.fireSwal("error", "Problem changing phrase");
        console.log(err);
      }
    },
    async saveUserPassword() {
      try {
        this.user.password = this.userPassword;
        this.updateUser(this.user);
        await api.users().updateUserPassword(this.user._id, this.userPassword);
        // this.editingPassword = false;
        this.showPassword = false;
        this.fireSwal("success", "Password changed");
      } catch (err) {
        this.fireSwal("error", "Problem changing password");
        console.log(err);
      }
    },
  },
};
</script>

