<template>
  <v-app-bar app :color="color" light>
    <div class="d-flex align-center">
      <!-- <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
          transition="scale-transition"
          width="40"
        /> -->
      <v-toolbar-title class="text-h5 text-white">Who's Out?</v-toolbar-title>
      <!-- <v-img
          alt="Vuetify Name"
          class="shrink mt-1 hidden-sm-and-down"
          contain
          min-width="100"
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-name-dark.png"
          width="100"
        /> -->
    </div>

    <v-spacer></v-spacer>
    <v-btn v-if="userExists" @click="$router.push({ name: 'Upload' })" icon>
      <v-icon :color="uploadActive ? 'white' : ''">mdi-plus-box</v-icon>
    </v-btn>
    <v-btn @click="changeColor" icon>
      <v-icon color="white">mdi-invert-colors</v-icon>
    </v-btn>
    <v-btn v-show="!userExists" @click="goToLogin" icon>
      <v-icon color="white">mdi-account</v-icon>
    </v-btn>
    <v-btn v-show="userExists" @click="logout" icon>
      <v-icon color="white">mdi-logout</v-icon>
    </v-btn>
  </v-app-bar>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Swal from "sweetalert2";

export default {
  data() {
    return {
      uploadActive: false,
    };
  },
  computed: {
    ...mapState(["user", "color"]),
    userExists() {
      return Object.keys(this.user).length;
    },
    currentRouteName() {
      return this.$route.name;
    },
  },
  mounted() {
    this.checkIfUploadActive();
  },
  methods: {
    ...mapActions(["resetUser", "toggleColor"]),
    checkIfUploadActive() {
      if (this.currentRouteName === "Upload") {
        this.uploadActive = true;
      } else {
        this.uploadActive = false;
      }
    },
    fireSwal(icon, title) {
      Swal.fire({
        icon,
        title,
        showConfirmButton: false,
        timer: 1500,
      });
    },
    changeColor() {
      this.toggleColor();
    },
    goToLogin() {
      this.$router.push({ name: "Login" });
    },
    logout() {
      this.fireSwal("success", `GoodBye ${this.user.username}`);
      this.resetUser();
      localStorage.removeItem("userId");
      this.$router.push({ name: "Login" });
    },
  },
  watch: {
    currentRouteName() {
      this.checkIfUploadActive();
    },
  },
};
</script>

