<template>
  <div class="post-form">
    <v-container>
      <img id="img" :src="displayImg" />
      <input
        type="file"
        class="form-control"
        ref="file"
        @change="handleFileChange"
      />

      <v-text-field label="Caption" type="text" v-model="caption" />
      <section id="btn-sect">
        <v-btn color="success" id="submit-btn" @click="submitForm"
          >submit</v-btn
        >
        <v-progress-circular
          v-show="uploading"
          id="progress"
          indeterminate
          :color="color"
        ></v-progress-circular>
      </section>
    </v-container>
  </div>
</template>

<script>
import api from "@/api/api.js";
import Swal from "sweetalert2";
import { mapState } from "vuex";

export default {
  name: "PostForm",
  data() {
    return {
      image: null,
      caption: "",
      imageReader: null,
      uploading: false,
      displayImg:
        "https://res.cloudinary.com/nadavkrashin/image/upload/v1634414280/Who%27s%20Out/default_kuew6t.jpg",
    };
  },
  computed: {
    ...mapState(["color"]),
  },
  methods: {
    handleFileChange() {
      this.image = this.$refs.file.files[0];

      if (this.image) {
        this.imageReader = new FileReader();
        this.imageReader.onloadend = () => {
          this.displayImg = this.imageReader.result;
        };
        this.imageReader.readAsDataURL(this.image);
      } else {
        this.displayImg =
          "https://res.cloudinary.com/nadavkrashin/image/upload/v1634414280/Who%27s%20Out/default_kuew6t.jpg";
      }
    },
    fireSwal(icon, title) {
      Swal.fire({
        icon,
        title,
        showConfirmButton: false,
        timer: 1500,
      });
    },
    async submitForm() {
      this.uploading = true;
      const formData = new FormData();
      formData.append("image", this.image);
      formData.append("caption", this.caption);

      try {
        await api.posts().addPost(formData);
        this.fireSwal("success", "Post added!");
        this.$router.push({ name: "Home" });
        this.uploading = false;
      } catch (err) {
        this.fireSwal("error", "Error posting!");
        this.uploading = false;
      }
    },
  },
};
</script>

<style scoped>
#img {
  display: block;
  width: 80vw;
  margin: 0 auto;
  margin-bottom: 3vh;
  margin-top: 1vh;
}
#btn-sect {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 5vh;
}
#progress {
  margin-left: 2vw;
}
</style>
